*{
    margin: 0;
    padding: 0;
   
}


html{
    background-image: url("Back.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 

}

.Main{
    width: 100%;
    display: flex;
   flex-direction: column;
    color: black;
}

.Store{
    background-color: black;
}

.Nav{
    align-items:center;
    justify-content:space-around;
    width: 100%;
   display: flex;
   flex-direction: row;
   background-color: black;
   color: aliceblue;
   padding: 10px;
   box-shadow: 0 3px 6px 0 #555;
}

.Logo{
    align-items: center;
    margin: 10px;
    background-color: black;
    
}

.Btn2{
    display: flex;
    background-color: black;
    color: aliceblue;
    size: 20px;
    justify-content:space-between;
   
    text-transform: uppercase;
    margin: 20px 0;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: 0.2s;
    border: 0;
    cursor: pointer;
}

.Btn2:hover{
    color:black;
    background: whitesmoke;
    box-shadow: 0 0 20px #0060ae,0 0 60px #0271cb,0 0 100px #0264b4;
    transition: all 1s ease;
    padding: 2px;
    
}


 
    
    